div, ul {
    width: 100%;
}

li {
    width: 33%;
}

nav, .bg-light {
    background-color: rgb(204, 234, 255)!important;
}
main {
    margin-top: 10px;
}

@media (min-width: 992px) {
    .container {
        max-width: 1000px;
    }
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: rgb(204, 234, 255);
}
div {
    text-align: center;
}
img {
    height: 200px;
    width: 200px;
    border: 5px solid rgb(204, 234, 255);
    margin: 10px;
}
